.no-overflow {
  overflow: hidden; }

.train-animation {
  overflow: hidden; }
  .train-animation img {
    animation: trainTransition 20s infinite linear;
    position: relative;
    top: -4px; }

@keyframes trainTransition {
  0% {
    transform: translateX(-200px); }
  50% {
    transform: translateX(calc(100vw + 110px)); }
  100% {
    transform: translateX(-110px); } }

.bg {
